import mpopup from '../../components/popup/popup.vue';
import LoginP from '../../static/js/pages/login/login.js';
import Popup from '@/static/js/components/popup/popup.js';
export default {
	components: {
		mpopup
	},
	data() {
		return {
			mobile: '',
			yzm: '',
			pass: '',
			coppass: '',
			codetxt: '发送验证码',
			time: 60,
			isclick: true,
			ispop: '',
			types: 'err',
			span: '这是一个土司',
			listArr: [],
			islogin: false,
			poptime: 2000
		};
	},
	methods: {
		delnewpsd:function(){
			this.pass="";
		},
		delpsd:function(){
			this.coppass="";
		},
		//手机验证发送验证码
		sendcode() {
			const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
			if (this.mobile == '') {
				Popup.setPopup(this, 'warn', '不能为空', this.poptime);
				return;
			}
			if (!reg.test(this.mobile)) {
				Popup.setPopup(this, 'warn', '手机号不正确', this.poptime);
				return;
			} else {
				let value = {
					mobile: this.mobile,
					event: 'register'
				};
				if (this.isclick) {
					this.timer();
					//数据处理
					LoginP.vcode(this, value);
				}
			}
		},
		//发送验证码计时器60s
		timer: function() {
			if (this.time > 0) {
				this.isclick = false;
				this.time--;
				this.codetxt = this.time + 's后重试';
				setTimeout(this.timer, 1000);
			} else {
				this.codetxt = '获取验证码';
				this.isclick = true;
				this.time = 60;
			}
		},
		//注册按钮
		registe: function() {
			//不能连续点击注册
			if (this.islogin) {
				Popup.setPopup(this, 'warn', '点击太快了', this.poptime);
				return;
			}
			//判断是否为空
			if (!this.pass || !this.coppass) {
				console.log('密码不能为空');
				Popup.setPopup(this, 'warn', '密码不能为空', this.poptime);
				return;
			}
			//判断两次密码是否一致
			if (this.pass != this.coppass) {
				console.log('两次密码不一致');
				Popup.setPopup(this, 'warn', '两次密码不一致', this.poptime);
				return;
			}
			//判断密码是否大于6位
			if (this.pass.length < 6 || this.coppass.length < 6) {
				console.log('密码应大于6位');
				Popup.setPopup(this, 'warn', '密码应大于', this.poptime);
				return;
			}
			let value = {
				mobile: this.mobile,
				captcha: this.yzm,
				password: this.coppass
			};
			this.islogin = true;
			//数据处理并回调
			LoginP.registe(this, value, revert => {
				console.log(revert);
			});
			setTimeout(() => {
				this.islogin = false;
			}, 3000);
		}
	}
};
